<template>
  <nav :class="{ active: navActive == 1 }" :style="{ top: navbarTop }">
    <a href="" id="logo">Mahmoud <span>Taha</span></a>
    <ul>
      <li>
        <a :class="{ active: activeSection === 'about-me' }" href="#about-me"
          >About</a
        >
      </li>
      <li>
        <a
          :class="{ active: activeSection === 'skills-section' }"
          href="#skills-section"
          >Skills</a
        >
      </li>
      <li>
        <a
          :class="{ active: activeSection === 'education-section' }"
          href="#education-section"
          >Education</a
        >
      </li>
      <li>
        <a
          :class="{ active: activeSection === 'experience-section' }"
          href="#experience-section"
          >Experience</a
        >
      </li>
      <li>
        <a
          :class="{ active: activeSection === 'contact-footer' }"
          href="#contact-footer"
          >Contact</a
        >
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      navActive: 0,
      activeSection: "about-me",
      lastScrollTop: 0,
      navbarTop: "0",
      isVisible: false,
    };
  },
  methods: {
    onScroll() {
      const scrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      if (scrollTop > this.lastScrollTop) {
        this.navbarTop = "-80px";
      } else {
        this.navbarTop = "15px";
      }
      this.lastScrollTop = scrollTop;
      if (scrollTop > 25) {
        this.navActive = 1;
      } else {
        this.navActive = 0;
      }

      const sections = document.querySelectorAll("section");
      let current = "";
      sections.forEach((section) => {
        const sectionTop = section.offsetTop;
        if (
          window.pageYOffset >= sectionTop - 0 ||
          document.documentElement.scrollTop >= sectionTop - 0
        ) {
          current = section.getAttribute("id");
        }
      });
      this.activeSection = current;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    this.onScroll();
  },
};
</script>

