  .loading-circle {
    position: relative;
    width: 20px;
    height: 20px;
    margin: 10px;
    border-radius: 50%;
  }
  .loading-circle:nth-of-type(1) {
    background-color: yellow;
    animation: loading 1s ease infinite;
  }
  .loading-circle:nth-of-type(2) {
    background-color: orange;
