    opacity: 1;
  }
}

@keyframes border-radius-animation {
  0% {
    border-radius: 70% 30% 70% 30% / 30% 70% 30% 70%;
  }
  25% {
    border-radius: 60% 40% 60% 60% / 40% 60% 40% 60%;
  }
  50% {
    border-radius: 50% 50% 50% 50% / 50% 70% 50% 70%;
  }
  75% {
    border-radius: 60% 40% 60% 40% / 40% 60% 40% 60%;
